ebkit-search-decoration{-webkit-appearance:none}::-webkit-file-upload-button{-webkit-appearance:button;font:inherit}details,menu{display:block}summary{display:list-item}canvas{display:inline-block}template{display:none}[hidden]{display:none}.clearfix:after{visibility:hidden;display:block;font-size:0;content:'';clear:both;height:0}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.sr-only{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;-webkit-clip-path:inset(100%);clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}.wrapper{max-width:1800px;margin:0 auto;width:80%}html{font-size:62.5%;padding-bottom:20px}body{background-color:#fff;color:#465853;font-size:1.5rem}header{background-color:#330072;min-height:50px;height:7.5vh;text-align:center}header a{display:inline-block;margin:12.5px auto}header img{width:75px}.formContainer{background-color:#f5f5f5;height:10vh;min-height:115px}.formContainer form{padding:30px 0}.formContainer label{display:block;margin-bottom:5px;font-weight:medium bold;color:#989b9a}.formContainer select,.formContainer button{height:30px;border:1px solid #f5f5f5}.formContainer select{background-color:#fff;width:40%;margin-right:5px}.formContainer button{background-color:#333;color:#fff;width:120px;font-size:1.2rem;font-weight:medium bold}.formContainer button:hover,.formContainer button:focus{background-color:#424242}.formContainer button:active{-webkit-transform:translateX(3px);transform:translateX(3px)}table{width:100%;border-collapse:collapse}table .col1,table .col2{width:20%}table .col3{width:50%}table .col4{width:10%}table th,table td{padding-left:5px;border-bottom:1px solid #f5f5f5}table thead{height:75px;box-shadow:-1px 2px 4px #bbb}table tr{height:50px;margin:50px 0}table th{text-align:left}table span{padding:5px 10px;border-radius:10px;margin-left:10px;text-transform:uppercase;font-family:"Noto Sans",sans-serif;font-size:1rem;font-weight:900}table .paid{background-color:#b1ddcb}table .delivered{background-color:#c4c4fe}table .cart{background-color:#ff8d41}table .first{background-color:#ffea6a;text-transform:none}table .market{border-radius:0px;background-color:#333;color:#fff;padding:5px}@media (max-width: 850px){.wrapper{width:95%}.formContainer select,.formContainer button{display:block;width:100%;margin-bottom:5px}table span{margin-left:0}table tr{height:100px}table .col1{width:25%}table .col3{width:15%}table .col3 span{display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}table .col3 span:first-of-type{margin-top:5px}table .col4{width:10%}}

